export default {
    props:{
        arr: {
            type: Array,
            default: []
        },
        accept: {
            type: Object,
            default: () => { return null }
        }
    },
    created(){
        if(this.accept){
            this.dataBack = this.accept
        }
    },
    data(){
        return {
            dataBack: null
        }
    },
    methods: {
        pick(i){
            this.dataBack = i
        },
        cancel(){
            this.$emit('close')
        },
        confirm(){
            this.$emit('confirm', this.dataBack)
        },
        add(){
            this.dataBack = 0
            this.$emit('add')
        }
    }
}



